				items: [
					{ product: { name: 'ÂÆ´‰øùÈ∏°‰∏Å', price: '25.00' }, quantity: 1 },
					{ product: { name: 'Á±≥È•≠', price: '3.00' }, quantity: 2 }
				]
			},
			{
				id: 'demo-2',
				creator: 'demo',
				creator_username: 'ÊºîÁ§∫Áî®Êà∑',
				status: 'COMPLETED',
				notes: 'ÁæéÂë≥ÁöÑÂçàÈ§ê',
				total_price: '32.00',
				created_at: '2024-06-21T12:15:00Z',
				items: [
					{ product: { name: 'Á∫¢ÁÉßËÇâ', price: '28.00' }, quantity: 1 },
					{ product: { name: 'Ê±§', price: '4.00' }, quantity: 1 }
