				creator: 'demo-partner',
				creator_username: 'TA',
				status: 'CONFIRMED',
				notes: 'TA‰∏∫‰Ω†ÁÇπÁöÑÁà±ÂøÉÈ§ê',
				total_price: '38.00',
				created_at: '2024-06-22T11:00:00Z',
				items: [
					{ product: { name: 'Á≥ñÈÜãÈáåËÑä', price: '26.00' }, quantity: 1 },
					{ product: { name: 'ËõãËä±Ê±§', price: '6.00' }, quantity: 2 }
