		];

		// ÊòæÁ§∫ÁöÑËÆ¢ÂçïÊï∞ÊçÆÔºàÊ†πÊçÆÁôªÂΩïÁä∂ÊÄÅÈÄâÊã©ÁúüÂÆûÊï∞ÊçÆÊàñÊ®°ÊãüÊï∞ÊçÆÔºâ
		const displaySentOrders = computed(() => {
			return isLoggedIn.value ? orderStore.sentOrders : mockSentOrders;
		});

		const displayReceivedOrders = computed(() => {
			return isLoggedIn.value ? orderStore.receivedOrders : mockReceivedOrders;
		});

		// Âà∑Êñ∞ËÆ¢ÂçïÊï∞ÊçÆ
		const refreshOrders = async () => {
			if (userStore.isLoggedIn) {
				console.log('Âà∑Êñ∞ËÆ¢ÂçïÊï∞ÊçÆ...');
				try {
					await Promise.all([
						orderStore.fetchSentOrders(),
						orderStore.fetchReceivedOrders()
					]);
					console.log('ËÆ¢ÂçïÊï∞ÊçÆÂà∑Êñ∞ÂÆåÊàê');
				} catch (error) {
					console.error('Âà∑Êñ∞ËÆ¢ÂçïÊï∞ÊçÆÂ§±Ë¥•:', error);
				}
			}
		};

		onMounted(() => {
			// Âè™ÊúâÁôªÂΩïÂêéÊâçËé∑ÂèñÁúüÂÆûËÆ¢ÂçïÊï∞ÊçÆ
			if (userStore.isLoggedIn) {
				orderStore.initOrders();
				refreshOrders();
			}
			// Êú™ÁôªÂΩïÊó∂ÊòæÁ§∫Ê®°ÊãüÊï∞ÊçÆÔºå‰∏çÈúÄË¶ÅÈ¢ùÂ§ñÊìç‰Ωú
		});

		// È°µÈù¢ÊòæÁ§∫Êó∂Âè™Âà∑Êñ∞‰∏ÄÊ¨°Êï∞ÊçÆÔºå‰∏çËÆæÁΩÆÂÆöÊó∂Âô®
		onShow(() => {
			console.log('ËÆ¢ÂçïÈ°µÈù¢ÊòæÁ§∫');
			if (userStore.isLoggedIn) {
				refreshOrders();
			}
		});

		// Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
		const goToLogin = () => {
			uni.navigateTo({
				url: '/pages/public/login'
			});
		};

		const handleTabClick = (item) => {
			currentTab.value = item.index;
		};

		const handleSwiperChange = (e) => {
			currentTab.value = e.detail.current;
		};

		const formatDate = (date) => {
			return dayjs(date).format('MM-DD HH:mm');
		};

		const getStatusText = (status) => {
			const statusMap = {
				'PENDING': 'ÂæÖÂ§ÑÁêÜ',
				'CONFIRMED': 'Â∑≤Á°ÆËÆ§',
				'ORDERED': 'Â∑≤‰∏ãÂçï',
				'COMPLETED': 'Â∑≤ÂÆåÊàê',
				'RATED': 'Â∑≤ËØÑ‰ª∑',
				'CANCELLED': 'Â∑≤ÂèñÊ∂à',
				// ÂÖºÂÆπÊóßÊ†ºÂºè
				'pending': 'ÂæÖÂ§ÑÁêÜ',
				'accepted': 'Â∑≤Êé•Êî∂',
				'completed': 'Â∑≤ÂÆåÊàê'
			};
			return statusMap[status] || 'Êú™Áü•';
		};

		const getStatusClass = (status) => {
			return `status-${status}`;
		};

		const navigateToDetail = (id) => {
			if (!isLoggedIn.value && id.startsWith('demo-')) {
				uni.showToast({
					title: 'ËØ∑ÁôªÂΩïÊü•ÁúãËÆ¢ÂçïËØ¶ÊÉÖ',
